// pages/dashboard/packages.tsx
import { useSession } from "next-auth/react";
import { useRouter } from "next/router";
import PackageCard from "@/components/PackageCard";

export default function Packages() {
  const { data: session } = useSession({ required: true });
  const router = useRouter();
  const userId = session!.user!.id;

  const packages = [
    // define en back o carga via SWR
    { id: "1", name: "Paquete 1", sessions: 4, price: 1400, inscription: 1000 },
    { id: "2", name: "Paquete 2", sessions: 8, price: 2250, inscription: 1000 },
    { id: "3", name: "Paquete 3", sessions: 12, price: 2500, inscription: 1000 },
  ];

  const buy = async (pkgId: string) => {
    const { id } = await fetch("/api/stripe/checkout", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ pkgId, userId }),
    }).then(r => r.json());
    const stripeJs = (await import("@stripe/stripe-js")).loadStripe(
      process.env.NEXT_PUBLIC_STRIPE_PUBLIC_KEY!
    );
    (await stripeJs)?.redirectToCheckout({ sessionId: id });
  };

  return (
    <div className="container py-5">
      <h2>Elige tu paquete</h2>
      <div className="row">
        {packages.map(p => (
          <div className="col-md-4" key={p.id}>
            <PackageCard pkg={p} onBuy={() => buy(p.id)} />
          </div>
        ))}
      </div>
    </div>
  );
}
